import React from "react";
import styled from "styled-components";
import { colorCodes } from "../data/misc";
import { Typography, Box } from "@material-ui/core";

export const ColoredText = styled(({ color, bgColor, ...other }) => (
  <Typography {...other} />
))`
  color: ${(props) => colorCodes[props.color] || props.color};
  background-color: ${(props) => colorCodes[props.bgColor]};
`;

export const ColoredBox = styled.div`
  background-color: ${(props) => colorCodes[props.bgColor]};
`;

export const Green = (props) => (
  <ColoredText component="span" variant="subtitle1" color="green" {...props} />
);

export const LGreen = (props) => (
  <ColoredText component="span" variant="body1" color="green" {...props} />
);

export const Link = styled.a`
  &:link,
  &:visited {
    color: ${colorCodes.green};
    text-decoration: none;
  }
  &:hover {
    color: ${colorCodes.lightRed};
  }
`;

export const Img = styled.img`
  width: ${(props) => props.width};
  height: ${(props) => props.height}};
  border-radius: ${(props) => props.borderRadius};
  background-color: ${(props) => props.bgColor || "transparent"};
  display: block;
`;

const StyledSvg = styled.svg`
  width: 1.25rem;
  height: 1.25rem;
  fill: ${(props) => colorCodes[props.rarity]};
`;

const Star = (props) => (
  <StyledSvg viewBox="0 0 24 24" rarity={props.rarity}>
    <path d="M12,17.27L18.18,21L16.54,13.97L22,9.24L14.81,8.62L12,2L9.19,8.62L2,9.24L7.45,13.97L5.82,21L12,17.27Z"></path>
  </StyledSvg>
);

export const StarLine = (props) => {
  const result = [];
  for (let i = 0; i < (props.rarity === "gold" ? 5 : 4); i++) {
    result.push(<Star key={i} rarity={props.rarity} />);
  }
  return (
    <Box display="flex" py={1}>
      {result}
    </Box>
  );
};

export const Ul = styled.ul`
  list-style-position: ${(props) => props.position};
  list-style-type: ${(props) => props.type};
`;
export const Table = styled.div`
  width: 100%;
  & > div:nth-child(2n + 1) {
    background-color: ${colorCodes.cardBg2};
  }
`;
