import { Green } from "../../styledCpns/DataDisplay";

const debuffsFrArts = {
  "Viridescent Venerer": [
    {
      desc: (
        <>
          Decreases opponent's Elemental RES to the element infused in the Swirl
          by <Green>40%</Green> for 10s.
        </>
      ),
      addPnes: (rdMult, input) => {
        rdMult[input + " RES"] -= 40;
      }
    }
  ]
};

export default debuffsFrArts;
