import { deepCopy, aHasB } from "../App";
import debuffsFrChar from "../data/characters/debuffsFrChar";
import { multMult } from "../data/characters/config";
import weapons from "../data/weapons";
import debuffsFrArts from "../data/artifacts/debuffsFrArts";
import { baseRxnDmg, tfmRxnBaseMult, ampRxnTrigElmts } from "../data/elements";
import { getXtraNaLv } from "../helpers";

function addElmtPnes(rdMult, { rsns, superconduct }) {
  for (let rsn of rsns) {
    if (rsn.name === "Enduring Rock" && rsn.activated) {
      rdMult["Geo RES"] -= 20;
      break;
    }
  }
  if (superconduct) rdMult["Physical RES"] -= 40;
}

function getDefMult(charLv, targetLv, defRd) {
  return (
    Math.round(
      ((charLv + 100) / ((1 - defRd / 100) * (targetLv + charLv + 200))) * 1000
    ) / 1000
  );
}

function getResMult(RES) {
  return RES < 0 ? 1 - RES / 2 : RES >= 0.75 ? 1 / (4 * RES + 1) : 1 - RES;
}

function getRdMult(char, charDC, party, artsDC, elmtC, { Level, ...rest }) {
  const rdMult = deepCopy(rest);
  rdMult.DEF = 0;
  charDC.forEach((ctrl, i) => {
    const debuff = debuffsFrChar[char.name][i];
    if (debuff.isGranted(char) && ctrl.activated) {
      debuff.addPnes(rdMult, true, char, ctrl.inputs);
    }
  });
  party.forEach((tm) => {
    if (tm.name) {
      tm.DC.forEach((ctrl, i) => {
        const debuff = debuffsFrChar[tm.name][i];
        if (ctrl.activated) {
          debuff.addPnes(rdMult, false, null, ctrl.inputs);
        }
      });
    }
  });
  artsDC.forEach((ctrl) => {
    if (ctrl.activated) {
      const debuff = debuffsFrArts[ctrl.set][ctrl.index];
      debuff.addPnes(rdMult, ctrl.input);
    }
  });
  addElmtPnes(rdMult, elmtC);

  const { DEF, ...others } = rdMult;
  rdMult.DEF = getDefMult(Number(char.level.split("/")[0]), Level, rdMult.DEF);
  for (let RES in others) {
    rdMult[RES] = getResMult(rdMult[RES] / 100);
  }
  return rdMult;
}

function getBase(ATRs, piece, level, { xtraLv = 0, xtraMult = 0 }) {
  const lv = level + xtraLv;
  let result =
    ATRs[piece.baseSType || "ATK"] *
    ((piece.baseMult * multMult[piece.multType][lv] + xtraMult) / 100);
  if (piece.baseFlat) {
    result += piece.baseFlat * multMult[piece.flatType][lv];
  }
  return result;
}

function getTotalBn(dmgTypes, dmgBnes, { xtraBn = 0 }) {
  let result = xtraBn;
  if (dmgTypes) {
    dmgTypes.forEach((dt) => {
      result += dmgBnes[dt] || 0;
    });
  }
  return 1 + result / 100;
}

function getRxnMult(ampRxn, dmgTypes, { vision, weapon }, infusion, rxnBnes) {
  if (
    ampRxn &&
    ((weapon === "catalyst" && aHasB(ampRxnTrigElmts[ampRxn], vision)) ||
      aHasB(dmgTypes, "CAS") ||
      aHasB(dmgTypes, "Elemental") ||
      aHasB(ampRxnTrigElmts[ampRxn], infusion))
  ) {
    return rxnBnes[ampRxn];
  }
  return 1;
}

function getReduction(
  rdMult,
  dmgTypes,
  { vision, weapon },
  infusion,
  { alterRdType }
) {
  const { DEF } = rdMult;
  if (alterRdType) {
    return DEF * rdMult[alterRdType];
  } else {
    if (
      weapon === "catalyst" ||
      aHasB(dmgTypes, "CAS") ||
      aHasB(dmgTypes, "Elemental")
    ) {
      return DEF * rdMult[vision + " RES"];
    } else if (aHasB(dmgTypes, "Physical")) {
      return DEF * rdMult["Physical RES"];
    } else if (aHasB(dmgTypes, "Various")) {
      return DEF;
    } else {
      return DEF * rdMult[infusion + " RES"];
    }
  }
}

function getCrit(crit, { xtraCR = 0, xtraCD = 0 }, dmgTypes, critBnes) {
  const result = { ...crit };
  result.CR += xtraCR;
  result.CD += xtraCD;
  for (let type of dmgTypes) {
    result.CR += critBnes[type]?.CR || 0;
    result.CD += critBnes[type]?.CD || 0;
  }
  return result;
}

export function getAtkDmg(
  char,
  charC,
  party,
  { vision, weapon, actvTalents },
  wp,
  artsDC,
  ATRs,
  dmgBnes,
  critBnes,
  rxnBnes,
  infusion,
  elmtC,
  target
) {
  const CD = ATRs["CRIT DMG"] / 100;
  const CR = ATRs["CRIT Rate"] / 100;
  const rdMult = getRdMult(char, charC.DC, party, artsDC, elmtC, target);
  const { ampRxn } = elmtC;

  const finalResult = {};
  actvTalents.forEach((tl) => {
    const { type } = tl;
    let level = char[type];
    if (type === "Normal Attack") {
      level += getXtraNaLv(char, party);
    }
    finalResult[type] = {};

    tl.stats.forEach((piece) => {
      const { dmgTypes, getTlBn } = piece;
      const { length } = dmgTypes;
      const tlBn = getTlBn ? piece.getTlBn(char, charC.BC, ATRs) : {};
      const base = getBase(ATRs, piece, level, tlBn);
      const totalBn = getTotalBn(dmgTypes, dmgBnes, tlBn);
      const rxnMult = length
        ? getRxnMult(ampRxn, dmgTypes, { vision, weapon }, infusion, rxnBnes)
        : 1;
      const reduction = length
        ? getReduction(rdMult, dmgTypes, { vision, weapon }, infusion, tlBn)
        : 1;
      const crit = getCrit({ CR, CD }, tlBn, dmgTypes, critBnes);

      finalResult[type][piece.name] = {
        "Non-crit":
          (base + (tlBn.xtraFlat || 0)) * totalBn * rxnMult * reduction,
        get Crit() {
          return length ? this["Non-crit"] * (1 + crit.CD) : 0;
        },
        get Average() {
          return length
            ? this["Non-crit"] * (1 + Math.min(crit.CR, 1) * crit.CD)
            : this["Non-crit"];
        }
      };
    });
  });

  finalResult["Reactions DMG"] = {};
  for (let rxn in tfmRxnBaseMult) {
    const charLv = char.level.split("/")[0];
    const base =
      baseRxnDmg[charLv] * tfmRxnBaseMult[rxn] * (1 + rxnBnes[rxn] / 100);
    finalResult["Reactions DMG"][rxn] = {
      "Non-crit": base,
      Crit: 0,
      Average: base
    };
  }
  const wpData = weapons[weapon][wp.name];
  if (wpData.pasvProcD) {
    finalResult["Weapon Attack Proc"] = {};
    const wpAtkProc = finalResult["Weapon Attack Proc"];
    wpData.pasvProcD.forEach((dmg) => {
      const base =
        ATRs[dmg.baseSType] *
        dmg.multiplier[wp.refinement] *
        (1 + dmgBnes[dmg.dmgType] / 100) *
        rdMult.DEF *
        rdMult["Physical RES"];
      wpAtkProc[dmg.name] = {
        "Non-crit": base,
        get Crit() {
          return this["Non-crit"] * (1 + CD);
        },
        get Average() {
          return base * (1 + (CR > 1 ? 1 : CR) * CD);
        }
      };
    });
  }
  return finalResult;
}
