import React, { useContext } from "react";
import { FixedContext, CharContext, PartyContext, deepCopy } from "../../App";
import { Typography } from "@material-ui/core";
import { ColoredText } from "../../styledCpns/DataDisplay";
import { Option } from "../../styledCpns/Inputs";
import {
  Setter,
  SetterInput,
  SetterCheckbox,
  SetterSelect,
  Section,
  NoContent
} from "../Modifiers.styled";
import { swirlables } from "../../data/elements";
import characters from "../../data/characters";
import buffsFrChar from "../../data/characters/buffsFrChar";
import { processNumInp } from "../../helpers";

export default function PartyBuff({ tryInfuse }) {
  const { panel2 } = useContext(FixedContext);
  const { parties } = useContext(PartyContext);
  const content = [];
  parties[panel2].forEach((tm, i) => {
    if (tm.BC.length) {
      content.push(<TmBuff key={i} tm={tm} tmI={i} tryInfuse={tryInfuse} />);
    }
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function TmBuff({ tm, tmI }) {
  const { setParties, panel2 } = useContext(FixedContext);
  const { charData } = useContext(CharContext);
  const subContent = [];

  tm.BC.forEach((ctrl, i) => {
    const buff = buffsFrChar[tm.name][ctrl.index];
    const { activated, inputs } = ctrl;
    const setters = buff.labels
      ? buff.labels.map((label, j) => {
          const type = buff.inputTypes[j];
          let input;
          if (type === "text") {
            input = (
              <SetterInput
                type="text"
                value={inputs[j]}
                onChange={(e) =>
                  setParties((prev) => {
                    const parties = deepCopy(prev);
                    parties[panel2][tmI].BC[i].inputs[j] = processNumInp(
                      e.target.value,
                      inputs[j],
                      buff.maxs[j]
                    );
                    return parties;
                  })
                }
              />
            );
          } else if (type === "check") {
            input = (
              <SetterCheckbox
                type="checkbox"
                checked={inputs[j]}
                onChange={() =>
                  setParties((prev) => {
                    const parties = deepCopy(prev);
                    parties[panel2][tmI].BC[i].inputs[j] = !inputs[j];
                    return parties;
                  })
                }
              />
            );
          } else {
            const options =
              type === "swirl"
                ? swirlables
                : Array.from({ length: buff.maxs[j] }, (_, i) => i + 1);
            input = (
              <SetterSelect
                value={inputs[j]}
                onChange={(e) =>
                  setParties((prev) => {
                    const parties = deepCopy(prev);
                    parties[panel2][tmI].BC[i].inputs[j] = e.target.value;
                    return parties;
                  })
                }
              >
                {options.map((opt) => (
                  <Option key={opt}>{opt}</Option>
                ))}
              </SetterSelect>
            );
          }
          return (
            <Setter key={j}>
              <Typography className="mr-3">{label}</Typography>
              {input}
            </Setter>
          );
        })
      : null;

    subContent.push(
      <Section
        key={i}
        checked={activated}
        handleCheck={() => {
          setParties((prev) => {
            const parties = deepCopy(prev);
            parties[panel2][tmI].BC[i].activated = !activated;
            const { canInfuse } = buff;
            if (canInfuse && canInfuse(charData)) {
              parties[panel2][tmI].inf = activated ? null : buff.infuseElmt;
            }
            return parties;
          });
        }}
        heading={buff.src}
        desc={buff.desc(false, inputs)}
        setters={setters}
      />
    );
  });
  return (
    <>
      <ColoredText
        className="pt-2 mb-m1"
        variant="h6"
        color={characters[tm.name].vision}
        align="center"
      >
        <strong>{tm.name.toUpperCase()}</strong>
      </ColoredText>
      {subContent}
    </>
  );
}
