import React, { useContext } from "react";
import { FixedContext, CharContext, CharCsContext, deepCopy } from "../../App";
import { Typography } from "@material-ui/core";
import { Option } from "../../styledCpns/Inputs";
import {
  Setter,
  SetterSelect,
  SetterInput,
  Section,
  NoContent
} from "../Modifiers.styled";
import debuffsFrChar from "../../data/characters/debuffsFrChar";
import { swirlables } from "../../data/elements";
import { processNumInp } from "../../helpers";

export default function SelfBuff(props) {
  const { setCharCs, panel2 } = useContext(FixedContext);
  const { char } = useContext(CharContext);
  const { charCs } = useContext(CharCsContext);
  const content = [];

  charCs[panel2].DC.forEach((ctrl, i) => {
    const debuff = debuffsFrChar[char.name][i];
    const { src, desc, inputTypes } = debuff;
    const { activated, inputs } = ctrl;
    if (debuff.isGranted(char)) {
      const setters = inputs
        ? inputs.map((input, k) => {
            let inpCpn;
            if (inputTypes[k] === "absorption") {
              inpCpn = (
                <SetterSelect
                  value={input}
                  onChange={(e) =>
                    setCharCs((prev) => {
                      const Cs = deepCopy(prev);
                      Cs[panel2].DC[i].inputs[k] = e.target.value;
                      return Cs;
                    })
                  }
                >
                  {swirlables.map((opt) => (
                    <Option key={opt}>{opt}</Option>
                  ))}
                </SetterSelect>
              );
            } else {
              inpCpn = (
                <SetterInput
                  type="text"
                  value={input}
                  onChange={(e) =>
                    setCharCs((prev) => {
                      const Cs = deepCopy(prev);
                      Cs[panel2].DC[i].inputs[k] = processNumInp(
                        e.target.value,
                        input,
                        inputTypes[k] === "level" ? 13 : 1 //
                      );
                      return Cs;
                    })
                  }
                />
              );
            }
            return (
              <Setter key={k}>
                <Typography className="mr-3">
                  {inputTypes[k] === "absorption"
                    ? "Element Absorbed"
                    : src + " level"}
                </Typography>
                {inpCpn}
              </Setter>
            );
          })
        : null;

      content.push(
        <Section
          key={i}
          checked={activated}
          handleCheck={() =>
            setCharCs((prev) => {
              const Cs = deepCopy(prev);
              Cs[panel2].DC[i].activated = !activated;
              return Cs;
            })
          }
          heading={src}
          desc={desc(true, char)}
          setters={setters}
        />
      );
    }
  });
  return content.length ? content : <NoContent type="debuffs" />;
}
