import React, { useContext } from "react";
import {
  FixedContext,
  CharContext,
  ElmtCsContext,
  ResultContext,
  deepCopy,
  aHasB
} from "../../App";
import { Green } from "../../styledCpns/DataDisplay";
import { Section, NoContent } from "../Modifiers.styled";
import { rsnBuffDesc } from "../../data/elements";

export default function ElmtBuff(props) {
  const { setElmtCs, panel2 } = useContext(FixedContext);
  const { vision } = useContext(CharContext).charData;
  const { elmtCs } = useContext(ElmtCsContext);
  const { rxnBnes } = useContext(ResultContext);
  const content = [];

  elmtCs[panel2].rsns.forEach((rsn, i) => {
    const { name, activated } = rsn;
    if (name) {
      content.push(
        <Section
          key={i}
          checked={activated}
          handleCheck={() =>
            setElmtCs((prev) => {
              const Cs = deepCopy(prev);
              Cs[panel2].rsns[i].activated = !activated;
              return Cs;
            })
          }
          heading={name}
          desc={rsnBuffDesc[name]}
        />
      );
    }
  });

  if (aHasB(["Pyro", "Hydro", "Cryo"], vision)) {
    const RxnBuff = (rxn) => {
      const activated = elmtCs[panel2].ampRxn === rxn;
      return (
        <Section
          key={rxn}
          checked={activated}
          handleCheck={() =>
            setElmtCs((prev) => {
              const Cs = deepCopy(prev);
              Cs[panel2].ampRxn = activated ? null : rxn;
              return Cs;
            })
          }
          heading={rxn}
          desc={
            <>
              Increases {vision} DMG by{" "}
              <Green>{Math.round(rxnBnes[panel2][rxn] * 1000) / 1000}</Green>{" "}
              times.
            </>
          }
        />
      );
    };
    content.push(
      {
        Pyro: ["Melt", "Vaporize"].map((rxn) => RxnBuff(rxn)),
        Hydro: RxnBuff("Vaporize"),
        Cryo: RxnBuff("Melt")
      }[vision]
    );
  }
  return content.length ? content : <NoContent type="buffs" />;
}
