import React, { useState, useContext } from "react";
import { deepCopy, FixedContext, UIContext } from "../../App";
import { Typography } from "@material-ui/core";
import { BtnOnCard, IconBtn } from "../../styledCpns/Inputs";
import { StyledOverview, StyledDetails, StyledLdt } from "./styles";
import Forge from "./Forge";
import artifacts from "../../data/artifacts";
import { initArts } from "../../data/initConfig";
import { wikia } from "../../data/misc";

export default function Loadouts({ data }) {
  const [coord, setCoord] = useState({ i: -1, type: "" });
  const { setInv, setArtsChest, setPoss } = useContext(FixedContext);
  const { shownSt } = useContext(UIContext).ui;
  return (
    <>
      <StyledOverview className="full-h">
        <div className="full-h overflow-auto">
          <div className="pr-3">
            {data.map((ldt, i) => (
              <Ldt
                key={i}
                ldt={ldt}
                i={i}
                coord={coord}
                setCoord={setCoord}
                remove={() => {
                  setInv((prev) => {
                    const inv = deepCopy(prev);
                    inv.Loadouts.splice(i, 1);
                    return inv;
                  });
                  setCoord({ i: -1 });
                }}
                pick={() => {
                  setArtsChest((prev) => {
                    const chest = deepCopy(prev);
                    chest[shownSt] = initArts(ldt.arts);
                    return chest;
                  });
                  setPoss(null);
                }}
              />
            ))}
          </div>
        </div>
      </StyledOverview>
      <StyledDetails forChar={false}>
        <div className="inner">
          {coord.i === -1 ? null : (
            <Forge
              isWp={false}
              info={data[coord.i].arts[coord.type]}
              index={coord.i}
              type={coord.type}
              setPosition={() => setCoord({ i: -1 })}
            />
          )}
        </div>
      </StyledDetails>
    </>
  );
}

function Ldt(props) {
  const { ldt, i, coord, setCoord } = props;
  const { setUI } = useContext(FixedContext);
  const chosenLdt = i === coord.i;
  return (
    <StyledLdt>
      <div className="btn_bar">
        <BtnOnCard bgColor="red" color="white" onClick={props.remove}>
          Remove
        </BtnOnCard>
        <BtnOnCard bgColor="lightGold" onClick={props.pick}>
          Pick
        </BtnOnCard>
      </div>
      <div className="grow-1">
        <Typography
          className={"mx-3 " + (ldt.name ? "my-1" : "my-2")}
          variant="h5"
          align="center"
        >
          {ldt.name}
        </Typography>
        <div className="line flex-wrap justify-center">
          {["flower", "plume", "sands", "goblet", "circlet"].map((type, j) => {
            const artDt = artifacts[ldt.arts?.[type]?.set]?.[type];
            return (
              <div key={j} className="ldt_item">
                {artDt ? (
                  <Inner
                    icon={artDt.icon}
                    bg="gold"
                    chosen={chosenLdt && coord.type === type}
                    handler={() => setCoord({ i, type })}
                  />
                ) : (
                  <Empty
                    handler={() =>
                      setUI((prev) => ({
                        ...prev,
                        modalName: type,
                        modalType: "archive/" + i
                      }))
                    }
                  />
                )}
              </div>
            );
          })}
        </div>
      </div>
    </StyledLdt>
  );
}

function Inner({ icon, bg, chosen, handler }) {
  return (
    <div className="ldt_inner flex" onClick={handler}>
      <img
        className={"item_img pointer " + bg + (chosen ? " glowing" : "")}
        src={wikia + icon + ".png"}
        alt=""
      />
    </div>
  );
}

function Empty({ handler }) {
  return (
    <div className="ldt_inner full-h">
      <IconBtn
        icon="fas fa-plus"
        fontSize="1.75rem"
        size="100%"
        color="rgb(255, 255, 255, 0.7)"
        bgColor="cardBg3"
        onClick={handler}
      />
    </div>
  );
}
