import weapons from "../data/weapons";
import artifacts from "../data/artifacts";
import buffsFrChar from "../data/characters/buffsFrChar";
import buffsFrArts from "../data/artifacts/buffsFrArts";
import { artMS } from "../data/artifacts/config";
import { tfmRxnBaseMult, ampRxnTrigElmts } from "../data/elements";
import { flatSTypes, pctSTypes } from "../data/misc";
import { incAllDmg } from "../helpers";

function initiate(char, charData, wp, wpData) {
  const ATRs = {};
  for (let type of flatSTypes.concat(pctSTypes)) {
    ATRs[type] = 0;
  }
  ATRs["CRIT Rate"] = 5;
  ATRs["CRIT DMG"] = 50;
  ATRs["Energy Recharge"] = 100;
  ATRs["Normal ATK SPD"] = 100;
  ATRs["Charged ATK SPD"] = 100;
  const stats = charData.stats[char.level];
  for (let key in stats) {
    ATRs[key] = (ATRs[key] || 0) + stats[key];
  }
  ATRs["Base ATK"] += wpData.mainStat.value[wp.level];
  return {
    ATRs,
    dmgBnes: {
      NA: 0,
      CA: 0,
      PA: 0,
      ES: 0,
      EB: 0
    },
    critBnes: { Elemental: {}, ES: { CR: 0 }, EB: { CR: 0 } }
  };
}

function addArtBnes(ATRs, dmgBnes, { sets, pieces }) {
  const artSBnes = { HP: 0, ATK: 0, DEF: 0 };
  for (let type in pieces) {
    const { set, level, mainSType, subS } = pieces[type];
    if (set) {
      artSBnes[mainSType] =
        (artSBnes[mainSType] || 0) + artMS[type][mainSType][level];
      for (let ss of subS) {
        artSBnes[ss.type] = (artSBnes[ss.type] || 0) + Number(ss.val);
      }
    }
  }
  ["HP", "ATK", "DEF"].forEach((type) => {
    const pctStats = artSBnes[type + "%"];
    if (pctStats) {
      artSBnes[type] += Math.round((ATRs["Base " + type] * pctStats) / 100);
    }
    delete artSBnes[type + "%"];
  });
  for (let type in artSBnes) {
    ATRs[type] += artSBnes[type];
  }
  const rxnBnes = {
    Superconduct: 0,
    Swirl: 0,
    "Electro-Charged": 0,
    Overloaded: 0,
    Shattered: 0,
    Melt: 0,
    Vaporize: 0
  };
  sets.forEach((set) => {
    for (let i = 0; i <= set.bonusLv; i++) {
      const { addBnes } = artifacts[set.name].setBonus[i];
      if (addBnes) addBnes(ATRs, dmgBnes, rxnBnes);
    }
  });
  return { artSBnes, rxnBnes };
}

function addWpBnes(ATRs, wpData, level) {
  wpData.subStats.forEach((stat) => {
    ATRs[stat.type] += stat.value[level];
  });
}

function addRsnBuffs(ATRs, dmgBnes, rsns) {
  rsns.forEach((rsn) => {
    if (rsn.activated) {
      if (rsn.name === "Fervent Flames") {
        ATRs["ATK%"] += 25;
      } else if (rsn.name === "Shattering Ice") {
        ATRs["CRIT Rate"] += 15;
      } else if (rsn.name === "Enduring Rock") {
        ATRs["Shield Strength"] += 15;
        incAllDmg(dmgBnes, 15);
      }
    }
  });
}

function addArtBuffs(ATRs, dmgBnes, { sets, BC, subBC }, cond) {
  subBC.forEach((ctrl) => {
    const buff = buffsFrArts[ctrl.set][ctrl.index];
    if (Boolean(buff.isDep) === cond && ctrl.activated) {
      buff.addBnes(ATRs, dmgBnes, ctrl.value);
    }
  });
  BC.forEach((ctrl) => {
    const buff = buffsFrArts[sets[0].name][ctrl.index];
    if (Boolean(buff.isDep) === cond && ctrl.activated) {
      buff.addBnes(ATRs, dmgBnes, ctrl.value);
    }
  });
}

function addWpBuffs(
  ATRs,
  dmgBnes,
  critBnes,
  wp,
  { addBnes, buffs },
  subWpBC,
  charData,
  cond
) {
  if (!cond && addBnes) {
    addBnes(ATRs, dmgBnes, wp.refinement, critBnes);
  }
  for (let wpType in subWpBC) {
    subWpBC[wpType].forEach((ctrl) => {
      const buff = weapons[wpType][ctrl.name].buffs[ctrl.index];
      if (Boolean(buff.isDep) === cond && ctrl.activated) {
        buff.addBnes(
          ATRs,
          dmgBnes,
          ctrl.refinement,
          ctrl.inputs,
          critBnes,
          charData
        );
      }
    });
  }
  wp.BC.forEach((ctrl) => {
    const buff = buffs[ctrl.index];
    if (Boolean(buff.isDep) === cond && ctrl.activated) {
      buff.addBnes(
        ATRs,
        dmgBnes,
        wp.refinement,
        ctrl.inputs,
        critBnes,
        charData
      );
    }
  });
}

function addCharBuffs(ATRs, dmgBnes, rxnBnes, char, charBC, party, cond) {
  if (!cond) {
    party.forEach((tm) => {
      tm.BC.forEach((ctrl) => {
        const { addBnes } = buffsFrChar[tm.name][ctrl.index];
        if (ctrl.activated && addBnes) {
          addBnes(ATRs, dmgBnes, rxnBnes, ctrl.inputs, false, char);
        }
      });
    });
  }
  charBC.forEach((ctrl) => {
    const buff = buffsFrChar[char.name][ctrl.index];
    if (
      Boolean(buff.isDep) === cond &&
      buff.isGranted(char) &&
      ctrl.activated &&
      buff.addBnes
    ) {
      buff.addBnes(ATRs, dmgBnes, rxnBnes, ctrl.value, true, char, charBC);
    }
  });
}

function calcFinalATRs(ATRs) {
  ["HP", "ATK", "DEF"].forEach((type) => {
    ATRs[type] += Math.round(
      ATRs["Base " + type] * (1 + ATRs[type + "%"] / 100)
    );
    delete ATRs[type + "%"];
  });
}

function calcFinalDmgBnes(ATRs, { vision, weapon }, infusion, dmgBnes) {
  const ownElmtDBn = ATRs[vision + " DMG Bonus"];
  const naDmgBn =
    weapon === "catalyst" ? ownElmtDBn : ATRs[infusion + " DMG Bonus"];
  dmgBnes.Elemental = ownElmtDBn;
  dmgBnes.Physical = ATRs["Physical DMG Bonus"];
  dmgBnes.NA += naDmgBn;
  dmgBnes.CA += naDmgBn;
  dmgBnes.PA += naDmgBn;
  dmgBnes.CAS = ownElmtDBn - naDmgBn;
}

export function getRxnBnFrEM(EM) {
  return {
    amplifying: Math.round((2780 * EM) / (EM + 1400)) / 10,
    transformative: Math.round((16000 * EM) / (EM + 2000)) / 10,
    shield: Math.round((4440 * EM) / (EM + 1400)) / 10
  };
}

function calcFinalRxnBnes(ATRs, rxnBnes, { vision }) {
  const { amplifying, transformative } = getRxnBnFrEM(
    ATRs["Elemental Mastery"]
  );
  Object.keys(tfmRxnBaseMult).forEach((rxn) => {
    rxnBnes[rxn] += transformative;
  });
  Object.keys(ampRxnTrigElmts).forEach((rxn) => {
    rxnBnes[rxn] += amplifying;
  });
  rxnBnes.Melt = (vision === "Pyro" ? 2 : 1.5) * (1 + rxnBnes.Melt / 100);
  rxnBnes.Vaporize =
    (vision === "Pyro" ? 1.5 : 2) * (1 + rxnBnes.Vaporize / 100);
}

export default function getStats(
  char,
  charData,
  charBC,
  wp,
  subWpBC,
  arts,
  rsns,
  party,
  infusion
) {
  const wpData = weapons[charData.weapon][wp.name];
  const { ATRs, dmgBnes, critBnes } = initiate(char, charData, wp, wpData);
  const { artSBnes, rxnBnes } = addArtBnes(ATRs, dmgBnes, arts);
  addWpBnes(ATRs, wpData, wp.level);
  addRsnBuffs(ATRs, dmgBnes, rsns);
  addArtBuffs(ATRs, dmgBnes, arts, false);
  addWpBuffs(ATRs, dmgBnes, critBnes, wp, wpData, subWpBC, charData, false);
  addCharBuffs(ATRs, dmgBnes, rxnBnes, char, charBC, party, false);
  calcFinalATRs(ATRs);

  addArtBuffs(ATRs, dmgBnes, arts, true);
  addWpBuffs(ATRs, dmgBnes, critBnes, wp, wpData, subWpBC, charData, true);
  addCharBuffs(ATRs, dmgBnes, rxnBnes, char, charBC, party, true);
  calcFinalDmgBnes(ATRs, charData, infusion, dmgBnes);
  calcFinalRxnBnes(ATRs, rxnBnes, charData);
  return { ATRs, dmgBnes, critBnes, artSBnes, rxnBnes };
}
